    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;
} */
@media (max-width: 1280px) {
  .offers {
    padding: 0px 80px;
    margin-bottom: 120px;
  }
  .offers-left h1 {
    font-size: 18px;
  }
  .offers-left p {
    font-size: 18px;
  }
  .offers-right img {
    width: 300px;
  }
  .offers-left button{
    width: 220px;
    height: 50px;
    font-size: 18px;
  }
  .offers-right{
    padding-top: 30px;
  }
}

@media (max-width: 1024px) {
    .offers {
      padding: 0px 60px;
      margin-bottom: 80px;
      height: 30vh;
    }
    .offers-left h1 {
      font-size: 30px;
    }
    .offers-left p {
      font-size: 16px;
    }
    .offers-right img {
      width: 200px;
    }
    .offers-left button{
      width: 160px;
      height: 40px;
      font-size: 16px;
    }
    .offers-right{
      padding-top: 10px;
    }
  }