.popular{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2px;
    height: 90vh;
}
.popular h1{
    color: #171717;
    font-size: 40px;
    font-weight: 600;
}
.popular hr{
    width: 150px;
    height: 3px;
    border-radius: 10px;
    background: #252525;
}
.popular-item{
    padding-top: 15px;
    display: flex;
    gap: 20px;
    justify-content: center;
    flex-wrap: wrap;
}
@media(max-width:1280px){
    .popular{
        height: 70vh;
    }
    .popular h1{
        font-size: 40px;
    }
    .popular hr{
        width: 160px;
        height: 4px;
    }
    .popular-item{
        gap: 20px;
        margin-top: 30px;
    }
}
@media(max-width:1024px){
    .popular{
        height: 60vh;
    }
    .popular h1{
        font-size: 30px;
    }
    .popular hr{
        width: 120px;
        height: 3px;
    }
    .popular-item{
        gap: 15px;
        margin-top: 20px;
    }
}
@media(max-width:800px){
    .popular{
        height: 50vh;
        gap: 6px;
    }
    .popular h1{
        font-size: 20px;
    }
    .popular hr{
        width: 100px;
    }
    .popular-item{
        gap: 5px;
    }
}
@media(max-width:500px){
    .popular{
        height: auto;
        gap: 6px;
        padding-bottom: 0px;
        padding-top: 0px;
    }
    
    .popular-item{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    }
}